import React from 'react';
import Cart from "./Cart";
import Navbar from "./Navbar";
import * as firebase from "firebase";

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      products: [],
      loading:true,
    }
  }

  componentDidMount(){
    // firebase
    // .firestore()
    // .collection('products')
    // .get()
    // .then((snapshot)=>{

    //   const products=snapshot.docs.map((doc)=>{
    //     const item = doc.data();
    //     item.id=doc.id;
    //     return item;
    //   },);

    //   this.setState({products,loading:false})

    // });

    firebase
    .firestore()
    .collection('products')
    .onSnapshot((snapshot)=>{

      const products=snapshot.docs.map((doc)=>{
        const item = doc.data();
        item.id=doc.id;
        return item;
      },);

      this.setState({products,loading:false})

    });
  }

  increaseQuantity = (product) => {
    product.qty++;
    this.setState(product);
  }

  decreaseQuantity = (product) => {
    if (product.qty <= 1) {
      return;
    }
    product.qty--;
    this.setState(product);
  }

  deleteProduct = (id) => {
    const products = this.state.products;
    const items = products.filter((item) => { return item.id !== id });
    this.setState({
      products: items
    });
  }

  getCartCount() {
    let sum = 0;
    for (const iterator of this.state.products) {
      sum += iterator.qty;
    }
    return sum;
  }

  getTotalPrice(){
    let sum = 0;
    for (const iterator of this.state.products) {
      sum += iterator.qty*iterator.price;
    }
    return sum;
  }

  render() {
    return (
      <div className="App">
        <Navbar cartQuant={this.getCartCount()} />
        <Cart products={this.state.products}
          increaseQuantity={this.increaseQuantity}
          decreaseQuantity={this.decreaseQuantity}
          deleteProduct={this.deleteProduct} />
        {this.state.loading && <h1>LOADING PRODUCTS ........</h1>}
        <div style={{fontSize:"20px", padding:"20px", textAlign:"right"}}>Total : {this.getTotalPrice()}</div>
      </div>
    );
  }


}

export default App;
